* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  background-color: #000000;
  color: #333;
}

.app {
  max-width: 100%;
  overflow-x: hidden;
}

/* Ensure accessibility */
a, button {
  outline: none;
}

a:focus, button:focus {
  outline: 2px solid #007bff;
  outline-offset: 2px;
}

/* Responsive container */
section {
  padding: 40px 20px;
  max-width: 1200px;
  margin: 0 auto;
}

@media (max-width: 768px) {
  section {
    padding: 20px 10px;
  }
}